import React, {useState, useEffect} from "react";
import Select from '@mui/material/Select';
import ArrowUp from "./ArrowUp";
import ArrowDown from "./ArrowDown"
import { makeStyles } from '@mui/styles';
import MenuItem from '@mui/material/MenuItem';

const useClasses = makeStyles(theme => ({
  selectStyle : {
    boxShadow: "5px 5px 9px rgb(179 202 224 / 29%)",
    borderRadius: "10px",
    width : "130px",
    paddingLeft: "5px",
    "&:before": {
      display: "none",
    },
    "& div" : {
      zIndex : "10",
      color:"#333333",
      paddingLeft : "10px",
    },
    "& svg" : {
      position : "absolute",
      left : "110px",
      zIndex : "1",
      bottom: "10px",
    }
  },
  dropdownStyle : {
    background : "#fff",
    color:"#333333",
    "& ul" : {
      "& option" : {
        cursor : "pointer",
        fontSize : "18px",
        
        "&:hover" : {
          background : "#E5E5E5"
        }
      }
    }
  },
}));




function App() {
  const [typeOpen, setTypeOpen] = useState(false);
  const [builder, setBuilder] = useState("Наружное")
  const classes = useClasses()

  // const menuProps = {
  //   getContentAnchorEl: null,
  //   anchorOrigin: {
  //     vertical: "bottom",
  //     horizontal: "left"
  //   },
  //   classes: { paper: classes.dropdownStyle }
  // };

  return (
    <Select

      IconComponent={() => (
        typeOpen ? <ArrowUp/> : <ArrowDown />
      )}
      onOpen={() => 
        setTypeOpen(true)
      }
      onClose={() => 
        setTypeOpen(false)
      }
      // MenuProps={menuProps}
      // className={classes.selectStyle}
      value={builder ? "Наружное" : "Внутреннее"}                      
      onChange={(e)=>
        setBuilder(e.target.value)
      }
    >
      <MenuItem selected value="Наружное">Наружное</MenuItem>
      <MenuItem value="Внутреннее">Внутреннее</MenuItem>
    </Select>
  );
}

export default App;
